import { CellAlign } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getUIGridLandscapeConfig() {
  const y = isSquareLikeScreen() ? 0.7 : 0.75;

  return {
    name: 'ui',
    cells: [
      {
        name: 'p_cta',
        align: CellAlign.RightCenter,
        bounds: { x: 0, y, width: 1, height: 0.15 }
      }
    ]
  };
}

export function getUIGridPortraitConfig() {
  return {
    name: 'ui',
    cells: [
      {
        name: 'p_cta',
        bounds: { x: 0, y: 0.85, width: 1, height: 0.15 }
      }
    ]
  };
}
