import { BoardState } from '../../../constants';
import { delayRunnable } from '../../../utils';
import { ObservableModel } from '../../observable-model';
import { CheckpointModel } from './checkpoint-model';

export class BoardModel extends ObservableModel {
  constructor(config) {
    super('BoardModel');

    this._checkpoints = null;
    this._keyCheckpoints = null;
    this._currentCheckpoint = null;
    this._currentKeyCheckpoint = null;
    this._config = config;
    this._state = BoardState.Unknown;

    this._initCheckpoints();
    this._initKeyCheckpoints();

    this.makeObservable();
  }

  get checkpoints() {
    return this._checkpoints;
  }

  get keyCheckpoints() {
    return this._keyCheckpoints;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  get config() {
    return this._config;
  }

  set config(value) {
    this._config = value;
  }

  get currentCheckpoint() {
    return this._currentCheckpoint;
  }

  set currentCheckpoint(value) {
    this._currentCheckpoint = value;
  }

  get currentKeyCheckpoint() {
    return this._currentKeyCheckpoint;
  }

  set currentKeyCheckpoint(value) {
    this._currentKeyCheckpoint = value;
  }

  getCheckpointByUuid(uuid) {
    return this._checkpoints.find(ch => ch.uuid === uuid);
  }

  getKeyCheckpointByUuid(uuid) {
    return this._keyCheckpoints.find(ch => ch.uuid === uuid);
  }

  getPointByUuid(uuid) {
    return [...this._keyCheckpoints, ...this._checkpoints].find(ch => ch.uuid === uuid);
  }

  retry() {
    this._state = BoardState.Restart;
    delayRunnable(2000, () => {
      this._state = BoardState.Idle;
    });
  }

  initialize() {
    this._state = BoardState.Idle;
  }

  resetCheckpoints() {
    this._currentCheckpoint = null;
    this._currentKeyCheckpoint = null;

    this._checkpoints.forEach(checkpoint => {
      checkpoint.reset();
    });
    this._keyCheckpoints.forEach(checkpoint => {
      checkpoint.reset();
    });
  }

  _initCheckpoints() {
    this._checkpoints = this._config.checkpoints.map(checkpoint => new CheckpointModel(checkpoint));
  }

  _initKeyCheckpoints() {
    this._keyCheckpoints = this._config.keyCheckpoints.map(checkpoint => new CheckpointModel(checkpoint));
  }
}
