import { ObservableModel } from '../observable-model';

export class IntroModel extends ObservableModel {
  constructor() {
    super('IntroModel');

    this._skip = false;

    this.makeObservable();
  }

  initialize() {
    super.initialize();
  }

  get skip() {
    return this._skip;
  }

  set skip(value) {
    this._skip = value;
  }
}
