import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getCTAGridLandscapeConfig() {
  const playBtnAlign = isSquareLikeScreen() ? CellAlign.Center : CellAlign.RightCenter;
  const shapePaddingY = isSquareLikeScreen() ? 0.1 : 0.175;
  const shapePaddingH = isSquareLikeScreen() ? 0.725 : 0.8;
  const boxY = isSquareLikeScreen() ? 0.69 : 0.75;
  const playBtnY = isSquareLikeScreen() ? 0.85 : boxY;
  const playBtnX = isSquareLikeScreen() ? 0 : 0.65;
  const playBtnW = isSquareLikeScreen() ? 1 : 0.35;
  const playBtnPaddingX = isSquareLikeScreen() ? 0.1 : 0.15;

  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'shape',
        scale: CellScale.ShowAll,
        bounds: { x: 0.1, y: 0.125, width: 0.8, height: 0.875 },
        padding: { x: 0.2, y: shapePaddingY, width: 0.6, height: shapePaddingH }
      },
      {
        name: 'play_button',
        align: playBtnAlign,
        padding: { x: playBtnPaddingX, y: 0.15, width: 0.8, height: 0.7 },
        bounds: { x: playBtnX, y: playBtnY, width: playBtnW, height: 0.15 }
      },
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'label_0',
        align: CellAlign.CenterBottom,
        padding: { x: 0.3, width: 0.4 },
        bounds: { x: 0, y: 0.05, width: 1, height: 0.08 }
      },
      {
        name: 'label_1',
        align: CellAlign.CenterTop,
        bounds: { x: 0, y: 0.13, width: 1, height: 0.1 },
        padding: { x: 0.325, y: 0, width: 0.35, height: 1 }
      },
      {
        name: 'box_bg',
        scale: CellScale.Fill,
        bounds: { x: 0, y: boxY, width: 1, height: 0.15 }
      },
      {
        name: 'box_label',
        bounds: { x: 0, y: boxY, width: 1, height: 0.15 }
      }
    ]
  };
}

