import { lego, not } from '@armathai/lego';
import { hasRetriesGuard } from '../../guards/game/has-retries-guard';
import { updateRetriesCountCommand } from '../game/update-retries-count-command';
import { loseBoardCommand } from './lose-board-command';
import { retryBoardCommand } from './retry-board-command';

export function failCommand() {
  lego.command
    //
    .guard(not(hasRetriesGuard))
    .execute(loseBoardCommand)

    .guard(hasRetriesGuard)
    .execute(updateRetriesCountCommand, retryBoardCommand);
}
