/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getMainGridConfig } from '../configs/grid-configs';
import { AdStatus } from '../constants';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';
import { postRunnable } from '../utils';
import { CTAView } from './cta/cta-view';
import { ForegroundView } from './foreground/foreground-view';
import { GameView } from './game/game-view';
import { UIView } from './ui/ui-view';

export class MainView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build();

    lego.event.on(ViewEvents.Game.Resize, this._onResize, this);
    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  get name() {
    return 'MainView';
  }

  getGridConfig() {
    return getMainGridConfig();
  }

  build() {
    super.build(this.getGridConfig());

    this.setChild('main', new GameView());
    this.setChild('main', new UIView());
    this.setChild('main', (this._ctaView = new CTAView()));
    this.setChild('main', new ForegroundView());

    postRunnable(CI_API.LU.refresh, CI_API.LU);
  }

  _onResize() {
    this.rebuild(this.getGridConfig());
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this.rebuildChild(this._ctaView);
        break;
      default:
    }
  }
}
