/* eslint-disable class-methods-use-this */
import { getCircleTextConfig } from '../../configs/text-configs';
import { fitText, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class ResultComponent extends Container {
  constructor(color, text) {
    super();

    this.bounceHideCompleteSignal = new Phaser.Signal();

    this._build(color, text);
    this.alpha = 0;
  }

  show() {
    this.showTw = this.game.add.tween(this.scale).from({ x: 0, y: 0 }, 700, Phaser.Easing.Back.Out, true);
    this.showTw.onStart.add(() => {
      this.alpha = 1;
    });

    return this.showTw;
  }

  hide() {
    this.hideTw = this.game.add.tween(this.scale).to({ x: 0, y: 0 }, 600, Phaser.Easing.Back.In, true, 200);
    return this.hideTw;
  }

  bounce() {
    this.show().onComplete.add(() => {
      this.hide().onComplete.add(() => {
        this.bounceHideCompleteSignal.dispatch();
      });
    });
  }

  _build(color, text) {
    this._buildCircle(color);
    this._buildText(text);
  }

  _buildCircle(color) {
    this._circle = this.game.add.graphics();
    this._circle.beginFill(color, 1);
    this._circle.drawCircle(0, 0, 370);
    this._circle.endFill();

    this.addChild(this._circle);
  }

  _buildText(text) {
    this._text = makeText(getCircleTextConfig(text));
    this._text.anchor.set(0.5);

    this.addChild(this._text);
    fitText(this._text, this._circle.width * 0.8);
  }
}
