import { getBoardConfig } from '../../configs/board-configs';
import { ObservableModel } from '../observable-model';
import { BoardModel } from './board/board-model';

export class GameModel extends ObservableModel {
  constructor() {
    super('GameModel');

    this._boardModel = null;
    this._retries = null;

    this.makeObservable();
  }

  get boardModel() {
    return this._boardModel;
  }

  get retries() {
    return this._retries;
  }

  set retries(value) {
    this._retries = value;
  }

  initialize() {
    this._retries = CI_API.Globals.PARAMS.retries;
    this._boardModel = new BoardModel(getBoardConfig());
    this._boardModel.initialize();
  }

  destroy() {
    this._boardModel.destroy();
    this._boardModel = null;
  }
}
