import { lego } from '@armathai/lego';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';
import { soundParamGuard } from '../guards/ad/sound-param-guard';
import { adLiveCommand } from './ad/ad-live-command';
import { adViewStateUpdateCommand } from './ad/ad-view-state-update-command';
import { ctaIdleTimeCommand } from './ad/cta/cta-idle-time-command';
import { ctaVisibleUpdateCommand } from './ad/cta/cta-visible-update-command';
import { initializeAdModelCommand } from './ad/initialize-ad-model-command';
import { setSoundStateCommand } from './ad/sound/set-sound-state-command';
import { initializeAnalyticsObservantCommand } from './observants/initialize-analytics-observant-command';
import { initializeSoundObservantCommand } from './observants/initialize-sound-observant-command';
import { initializeWrapperObservantCommand } from './observants/initialize-wrapper-observant-command';
import { resizeCommand } from './resize-command';
import { initializePhaserStatesCommand } from './states/initialize-phaser-states-command';
import { initializeNinePatchesCommand } from './view/initialize-ninepatches-command';
import { initializeParticlesCommand } from './view/initialize-particles-command';
import { onRetryClickCommand } from './view/on-retry-click-command';
import { patchTextForLocalizationCommand } from './view/patch-text-for-localization-command';
import { userInteractionCommand } from './view/user-interaction-command';

export function startupCommand() {
  lego.command

    // .execute(addOverlayCommand)
    .execute(patchTextForLocalizationCommand)
    .execute(initializeNinePatchesCommand)
    .execute(initializeParticlesCommand)
    .execute(initializeAdModelCommand)
    .execute(initializePhaserStatesCommand)
    .execute(initializeWrapperObservantCommand)
    .execute(initializeAnalyticsObservantCommand)

    .guard(soundParamGuard)
    .execute(initializeSoundObservantCommand)

    .on(ViewEvents.Ad.Live, adLiveCommand)
    .on(ViewEvents.Game.Resize, resizeCommand)
    .on(ViewEvents.Game.UserInteraction, userInteractionCommand)
    .on(ViewEvents.GameState.CtaIdleTime, ctaIdleTimeCommand)
    .on(ModelEvents.AdModel.ViewStateUpdate, adViewStateUpdateCommand)
    .on(ViewEvents.SoundView.Click, setSoundStateCommand)
    .on(ModelEvents.CtaModel.VisibleUpdate, ctaVisibleUpdateCommand)
    .on(ViewEvents.CtaView.RetryClick, onRetryClickCommand);
}
