import { lego } from '@armathai/lego';
import { AdViewState, GameOverReasons } from '../constants';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';
import { store } from '../models/store';

function hideSplash() {
  wrapper_hide_splash();
}

function onLoadProgress(progress) {
  wrapper_load_progress(progress);
}

function onLoadComplete() {
  wrapper_preload_complete();
}

function onViewStateUpdate(state) {
  state === AdViewState.Create && hideSplash();
}

function clickGo() {
  wrapper_click_go();
}

function onCtaVisibleUpdate(visible) {
  if (!visible) {
    return;
  }

  if (store.ad.cta.reason === GameOverReasons.Unknown) {
    // eslint-disable-next-line no-console
    console.error('Please provide game over reason');
  }

  wrapper_mark_cta(store.ad.cta.reason);
}

function markInteraction() {
  wrapper_mark_interaction();
}

export function WrapperObservant() {
  lego.event
    .on(ModelEvents.LoadModel.ProgressUpdate, onLoadProgress, this)
    .on(ModelEvents.LoadModel.CompleteUpdate, onLoadComplete, this)
    .on(ModelEvents.AdModel.ViewStateUpdate, onViewStateUpdate, this)
    .on(ViewEvents.PersistentCtaView.Click, clickGo, this)
    .on(ViewEvents.CtaView.ScreenClick, clickGo, this)
    .on(ViewEvents.CtaView.PlayClick, clickGo, this)
    .on(ModelEvents.CtaModel.VisibleUpdate, onCtaVisibleUpdate, this)
    .on(ViewEvents.Game.UserInteraction, markInteraction, this);
}
