import { lego, not } from '@armathai/lego';
import { currentKeyCheckpointGuard } from '../../guards/board/current-key-checkpoint-guard';
import { isLastNotCheckedKeyCheckpointGuard } from '../../guards/board/is-last-not-checked-key-checkpoint-guard';
import { store } from '../../models/store';
import { setCurrentKeyCheckpointCommand } from '../board/set-current-key-checkpoint-command';
import { winBoardCommand } from '../board/win-board-command';

/* eslint-disable no-underscore-dangle */
export function onKeyCheckpointIntersectedCommand(uuid) {
  const { boardModel } = store.game;
  const checkpoint = boardModel.getKeyCheckpointByUuid(uuid);

  lego.command
    //

    .payload(checkpoint)
    .guard(not(currentKeyCheckpointGuard))
    .execute(setCurrentKeyCheckpointCommand)

    .payload(checkpoint)
    .guard(isLastNotCheckedKeyCheckpointGuard)
    .execute(winBoardCommand);
}
