import { lego } from '@armathai/lego';
import { currentKeyCheckpointGuard } from '../../guards/board/current-key-checkpoint-guard';
import { setCurrentKeyCheckpointCommand } from '../board/set-current-key-checkpoint-command';
import { updateKeyCheckpointIntersectionCountCommand } from '../board/update-key-checkpoint-intersection-count-command';

/* eslint-disable no-underscore-dangle */
export function onNoKeyCheckpointsIntersectionCommand() {
  lego.command
    //

    .guard(currentKeyCheckpointGuard)
    .execute(updateKeyCheckpointIntersectionCountCommand)

    .payload(null)
    .execute(setCurrentKeyCheckpointCommand);
}
