/* global Phaser, imageLoader, embedded */

(() => {
  // Decorate Phaser Render
  const { generateTilingTexture } = Phaser.TileSprite.prototype;
  const renderCanvas = Phaser.Sprite.prototype._renderCanvas;
  const matrixPool = [];
  const rectPool = [];
  const scalePool = {};

  const scaleJson = json => {
    const { frames } = json;

    for (const name in frames) {
      if (!Object.prototype.hasOwnProperty.call(frames, name) || !frames[name].frame) continue;
      const frame = frames[name];
      const scale = getScale(json, name);

      frame.frame.x /= scale;
      frame.frame.y /= scale;
      frame.frame.w /= scale;
      frame.frame.h /= scale;
      frame.spriteSourceSize.x /= scale;
      frame.spriteSourceSize.y /= scale;
      frame.spriteSourceSize.w /= scale;
      frame.spriteSourceSize.h /= scale;
      frame.sourceSize.w /= scale;
      frame.sourceSize.h /= scale;
    }
  };

  // Normalize scale for all embedded atlas jsons
  const scaleAllJsons = () => {
    for (const embeddedKey in embedded) {
      const json_data = embedded[embeddedKey];
      if (json_data.meta && json_data.frames) {
        scaleJson(json_data);
      }
    }
  };

  // Return sprite independent scale
  const getScale = (jsonData, frameName) => {
    if (scalePool[frameName]) {
      return scalePool[frameName];
    }

    let scale = 1;
    if (jsonData && jsonData.frames && jsonData.frames[frameName] && jsonData.frames[frameName].scale) {
      scale = jsonData.frames[frameName].scale;
    }

    scalePool[frameName] = scale;
    return scale;
  };

  const multiply = (rect, sc) => {
    rect.x *= sc;
    rect.y *= sc;
    rect.width *= sc;
    rect.height *= sc;
  };

  Phaser.Sprite.prototype._renderCanvas = function(renderSession, matrix) {
    if (
      !this.visible ||
      this.alpha === 0 ||
      !this.renderable ||
      this.texture.crop.width <= 0 ||
      this.texture.crop.height <= 0
    )
      return;

    const { texture } = this;
    const wt = this.worldTransform;
    const json = embedded[texture.baseTexture.source.name];
    const sc = getScale(json, this.frameName);
    const crop = (rectPool.pop() || new Phaser.Rectangle()).copyFrom(texture.crop);
    const trim = (rectPool.pop() || new Phaser.Rectangle()).copyFrom(texture.trim || texture.frame);
    const m = (matrixPool.pop() || new Phaser.Matrix()).setTo(wt.a / sc, wt.b / sc, wt.c / sc, wt.d / sc, wt.tx, wt.ty);

    multiply(texture.crop, sc);
    multiply(texture.trim || texture.frame, sc);

    renderCanvas.call(this, renderSession, matrix || m);

    crop.copyTo(texture.crop);
    trim.copyTo(texture.trim || texture.frame);

    rectPool.push(crop, trim);
    matrixPool.push(m);
  };

  Phaser.TileSprite.prototype.generateTilingTexture = function(forcePowerOfTwo) {
    if (!this.texture.baseTexture.hasLoaded) return;

    const { texture } = this;
    const json = embedded[texture.baseTexture.source.name];
    const sc = getScale(json, this.frameName);
    const crop = (rectPool.pop() || new Phaser.Rectangle()).copyFrom(texture.crop);

    multiply(texture.crop, sc);
    generateTilingTexture.call(this, forcePowerOfTwo);
    crop.copyTo(texture.crop);
    rectPool.push(crop);
  };

  const imageRenderCanvas = Phaser.Button.prototype._renderCanvas;
  const imageMatrixPool = [];
  const imageRectPool = [];

  Phaser.Button.prototype._renderCanvas = function(renderSession, matrix) {
    if (
      !this.visible ||
      this.alpha === 0 ||
      !this.renderable ||
      this.texture.crop.width <= 0 ||
      this.texture.crop.height <= 0
    )
      return;

    const { texture } = this;
    const wt = this.worldTransform;
    const json = embedded[texture.baseTexture.source.name];
    const sc = getScale(json, this.frameName);
    const crop = (imageRectPool.pop() || new Phaser.Rectangle()).copyFrom(texture.crop);
    const trim = (imageRectPool.pop() || new Phaser.Rectangle()).copyFrom(texture.trim || texture.frame);
    const m = (imageMatrixPool.pop() || new Phaser.Matrix()).setTo(
      wt.a / sc,
      wt.b / sc,
      wt.c / sc,
      wt.d / sc,
      wt.tx,
      wt.ty
    );

    multiply(texture.crop, sc);
    multiply(texture.trim || texture.frame, sc);
    imageRenderCanvas.call(this, renderSession, matrix || m);

    crop.copyTo(texture.crop);
    trim.copyTo(texture.trim || texture.frame);

    imageRectPool.push(crop, trim);
    imageMatrixPool.push(m);
  };

  // Override Spine.createSprite method for supporting independent asset scaling
  const overrideSpineLibrary = function() {
    let spineLibraryAdded = false;
    try {
      spineLibraryAdded = spine || null;
    } catch (e) {}

    if (spineLibraryAdded) {
      PhaserSpine.Spine.prototype.createSprite = function(slot, attachment) {
        const descriptor = attachment.rendererObject;
        const baseTexture = descriptor.page.rendererObject;
        const spriteRect = new PIXI.Rectangle(
          descriptor.x,
          descriptor.y,
          descriptor.rotate ? descriptor.height : descriptor.width,
          descriptor.rotate ? descriptor.width : descriptor.height
        );

        // Get asset scale and adjust it to the sprite rect
        const sc = getScale(null, `${descriptor.name}.png`) || 1;
        multiply(spriteRect, sc);

        const spriteTexture = new PIXI.Texture(baseTexture, spriteRect);

        const sprite = new Phaser.Sprite(this.game, 0, 0, spriteTexture);
        const baseRotation = descriptor.rotate ? Math.PI * 0.5 : 0.0;

        // Divide on asset scale to get the original sprite size
        sprite.scale.x = attachment.scaleX / this.imageScale / sc;
        sprite.scale.y = attachment.scaleY / this.imageScale / sc;
        sprite.rotation = baseRotation;
        sprite.anchor.x = (0.5 * descriptor.originalWidth - descriptor.offsetX) / descriptor.width;
        sprite.anchor.y = (0.5 * descriptor.originalHeight - descriptor.offsetY) / descriptor.height;
        sprite.alpha = attachment.a;
        if (descriptor.rotate) {
          const x1 = sprite.scale.x;
          sprite.scale.x = sprite.scale.y;
          sprite.scale.y = x1;
        }
        slot.sprites = slot.sprites || {};
        slot.sprites[descriptor.name] = sprite;
        return sprite;
      };
    }
  };

  scaleAllJsons();
  overrideSpineLibrary();
})();
