import { lego, not } from '@armathai/lego';
import { alreadyCheckedGuard } from '../../guards/board/already-checked-guard';
import { currentCheckpointGuard } from '../../guards/board/current-checkpoint-guard';
import { store } from '../../models/store';
import { failCommand } from '../board/fail-command';
import { setCurrentCheckpointCommand } from '../board/set-current-checkpoint-command';

/* eslint-disable no-underscore-dangle */
export function onCheckpointIntersectedCommand(uuid) {
  const { boardModel } = store.game;
  const checkpoint = boardModel.getCheckpointByUuid(uuid);

  lego.command
    //

    .payload(checkpoint)
    .guard(not(currentCheckpointGuard))
    .execute(setCurrentCheckpointCommand)

    .payload(uuid)
    .guard(alreadyCheckedGuard)
    .execute(failCommand);
}
