import { lego } from '@armathai/lego';
import { currentCheckpointGuard } from '../../guards/board/current-checkpoint-guard';
import { setCurrentCheckpointCommand } from '../board/set-current-checkpoint-command';
import { updateCheckpointIntersectionCountCommand } from '../board/update-checkpoint-intersection-count-command';

/* eslint-disable no-underscore-dangle */
export function onNoCheckpointsIntersectionCommand() {
  lego.command
    //

    .guard(currentCheckpointGuard)
    .execute(updateCheckpointIntersectionCountCommand)

    .payload(null)
    .execute(setCurrentCheckpointCommand);
}
