import { store } from '../../models/store';

export function isLastNotCheckedKeyCheckpointGuard(checkpoint) {
  const { keyCheckpoints } = store.game.boardModel;
  const keyPoints = keyCheckpoints.filter(ch => ch.keyPoint);
  const notCheckedCheckpoints = keyPoints.filter(ch => !ch.checked);

  return notCheckedCheckpoints.length === 1 && notCheckedCheckpoints[0].uuid === checkpoint.uuid;
}
