const ctaBtn = 'ui/orange_btn.png';
const pCta = 'ui/p_CTA.png';

export function getButtonPatchConfig(text) {
  let width = 475;

  switch (text) {
    case 'Play':
      width = 300;
      break;
    default:
      break;
  }

  return {
    width,
    height: 115,
    frame: pCta
  };
}

export function getPCTAButtonPatchConfig() {
  return {
    width: 475,
    height: 115,
    frame: pCta
  };
}

