import { lego } from '@armathai/lego';
import { BoardState } from '../constants';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';

let sound;
let isScratchSoundPlaying = false;

function play(id, volume) {
  return sound.play(id, volume);
}

function stop(id, volume) {
  sound.stop(id, volume);
}

function onLoadCompleteUpdate(complete) {
  if (!complete) {
    return;
  }
  sound = soundLoader.addAudioSprite('audio');
}

function playGameTheme() {
  play('game-theme', 1);
}

function playBoardFail() {
  stop('draw', 1);
  play('fail_2', 1);
}

function playWinCircle() {
  stop('draw', 1);
  play('win', 1);
}

function playFailCircle() {
  stop('draw', 1);
  play('fail_1', 1);
}

function onScratchStart() {
  if (!isScratchSoundPlaying) {
    isScratchSoundPlaying = true;
    const s = play('draw', 1);

    // eslint-disable-next-line no-shadow
    s.onStop.add((sound, name) => {
      if (name === 'draw') {
        isScratchSoundPlaying = false;
      }
    });
  }
}

function onBoardStateUpdate(state) {
  switch (state) {
    case BoardState.Lose:
    case BoardState.Restart:
      playFailCircle();
      break;
    case BoardState.Win:
      playWinCircle();
      break;

    default:
      break;
  }
}

export function SoundObservant() {
  lego.event.on(ModelEvents.LoadModel.CompleteUpdate, onLoadCompleteUpdate, this);
  lego.event.once(ViewEvents.Game.UserInteraction, playGameTheme, this);
  lego.event.on(ViewEvents.BoardView.Fail, playBoardFail, this);
  lego.event.on(ModelEvents.BoardModel.StateUpdate, onBoardStateUpdate);
  lego.event.on(ViewEvents.BoardView.Scratching, onScratchStart, this);
}
