import { CellScale } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getGameGridLandscapeConfig() {
  const resultY = isSquareLikeScreen() ? 0.15 : 0.22;
  const resultH = isSquareLikeScreen() ? 0.6 : 0.55;
  const boardPaddingY = isSquareLikeScreen() ? 0.125 : 0.15;
  const boardPaddingH = isSquareLikeScreen() ? 0.7 : 0.75;
  const boxY = isSquareLikeScreen() ? 0.7 : 0.75;
  return {
    // debug: { color: 0x2fc900 },
    name: 'game',
    cells: [
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'board',
        scale: CellScale.ShowAll,
        bounds: { x: 0.1, y: 0.125, width: 0.8, height: 0.875 },
        padding: { x: 0.2, y: boardPaddingY, width: 0.6, height: boardPaddingH }
      },
      {
        name: 'result',
        bounds: { x: 0, y: resultY, width: 1, height: resultH }
      },
      {
        name: 'box_bg',
        scale: CellScale.Fill,
        bounds: { x: 0, y: boxY, width: 1, height: 0.15 }
      },
      {
        name: 'box_label',
        bounds: { x: 0, y: boxY, width: 1, height: 0.15 }
      }
    ]
  };
}

export function getGameGridPortraitConfig() {
  return {
    // debug: { color: 0x2fc900 },
    name: 'game',
    cells: [
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'board',
        scale: CellScale.ShowAll,
        bounds: { x: 0.05, y: 0.3, width: 0.9, height: 0.55 }
      },
      {
        name: 'result',
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'box_bg',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0.725, width: 1, height: 0.125 }
      },
      {
        name: 'box_label',
        bounds: { x: 0, y: 0.725, width: 1, height: 0.125 }
      }
    ]
  };
}
