/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { AdStatus, AdViewState } from '../constants';
import { ViewEvents } from '../events/view-events';
import { store } from '../models/store';
import { MainView } from '../objects/main-view';
import { manageUniversalTweens } from '../utils';

export class GameState extends Phaser.State {
  constructor() {
    super();

    this._adModel = store.ad;
    this._mainView = null;
  }

  init() {
    super.init();

    store.ad.viewState = AdViewState.Init;
  }

  preload(game) {
    super.preload(game);

    store.ad.viewState = AdViewState.Preload;
  }

  create(game) {
    super.create(game);

    this._mainView = new MainView();

    this.input.onDown.add(() => {
      lego.event.emit(ViewEvents.Game.UserInteraction);
    });

    this.game.world.handleResize = orientation => {
      manageUniversalTweens();
      lego.event.emit(ViewEvents.Game.Resize, orientation);
    };

    store.ad.viewState = AdViewState.Create;
  }

  shutdown(game) {
    store.ad.viewState = AdViewState.ShutDown;

    super.shutdown(game);
  }

  update() {
    const { Globals, game } = CI_API;

    super.update(game);

    if (ad_state === 'live' && this._adModel.status !== AdStatus.Cta) {
      if (game.time.totalElapsedSeconds() - Globals.LAST_INTERACTION_TIME > Globals.PARAMS.cta_on_idle_time * 0.001) {
        lego.event.emit(ViewEvents.GameState.CtaIdleTime);
      }
    }
  }

  victory(_reason) {
    //
  }

  defeat(_reason) {
    //
  }
}
