export class BoardView extends Container {
  constructor() {
    super();

    this._drawnPoints = [];
    this._checkpoints = null;
    this._keyCheckpoints = null;

    lego.event.on(
      ModelEvents.CheckpointModel.IntersectionsCountUpdate,
      this._onCheckpointIntersectionCountUpdate,
      this
    );
    lego.event.on(ModelEvents.CtaModel.PreVisibleUpdate, this._onCtaPreVisibleUpdate, this);
    lego.event.on(ModelEvents.BoardModel.StateUpdate, this._onBoardModelStateUpdate, this);

    this._build();
  }

  get name() {
    return 'BoardView';
  }

  getBounds() {
    return this._bg.getBounds();
  }

  getCheckpointByUuid(uuid) {
    return this._checkpoints.find(ch => ch.uuid === uuid);
  }

  getKeyCheckpointByUuid(uuid) {
    return this._keyCheckpoints.find(ch => ch.uuid === uuid);
  }

  getPointByUuid(uuid) {
    return [...this._checkpoints, ...this._keyCheckpoints].find(ch => ch.uuid === uuid);
  }

  hide() {
    return hideToLeft(this, 600);
  }

  _onCtaPreVisibleUpdate(value) {
    if (value) {
      lego.event.off(ModelEvents.CtaModel.PreVisibleUpdate, this._onCtaPreVisibleUpdate, this);
      lego.event.off(ModelEvents.BoardModel.StateUpdate, this._onBoardModelStateUpdate, this);

      this._removeListeners();
    }
  }

  _onCheckpointIntersectionCountUpdate(value, prevValue, uuid) {
    const checkpoint = this.getPointByUuid(uuid);

    checkpoint.setChecked(value);
  }

  _onBoardModelStateUpdate(state) {
    switch (state) {
      case BoardState.Win:
        this._removeListeners();
        break;
      case BoardState.Restart:
        this._removeListeners();
        postRunnable(() => {
          this._destroyHitAreas();
          this._playLoseFX();
        });
        break;
      case BoardState.Lose:
        this._removeListeners();
        this._playLoseFX();
        break;
      case BoardState.Idle:
        this._resetDrawnPoints();
        this._buildHitAreas();
        this._addListeners();
        break;

      default:
        break;
    }
  }

  _resetDrawnPoints() {
    this._drawnPoints.length = 0;
  }

  _destroyHitAreas() {
    this._hitArea.destroy();
    this._hitAreaInnerBoundaries.destroy();
    this._outThresholdZone.destroy();
    // this._deadZone.destroy();

    this._hitArea = null;
    this._hitAreaInnerBoundaries = null;
    this._outThresholdZone = null;
    this._deadZone = null;
  }

  _buildHitAreas() {
    this._hitArea = this._buildHitArea(hitAreas[CI_API.Globals.PARAMS.difficulty_type], 0x00ff00);
    this._hitAreaInnerBoundaries = this._buildHitArea(
      hitAreasInnerBoundaries[CI_API.Globals.PARAMS.difficulty_type],
      0xff0000
    );
    this._outThresholdZone = this._buildHitArea(hitAreas.easy, 0x0000ff);
    this._innerThresholdZone = this._buildHitArea(hitAreasInnerBoundaries.easy, 0x000000);
    this._deadZone = this._buildHitArea(deadZone, 0x333300);
  }

  _removeListeners() {
    if (!this._hitArea) {
      return;
    }

    this.game.input.deleteMoveCallback(this._onMove, this);

    this._hitArea.events.onInputUp.remove(this._lose, this);
    this._hitArea.events.onInputDown.remove(this._onDown, this);

    this._hitArea.events.onInputUp.remove(this._lose, this);
    this._innerThresholdZone.events.onInputUp.remove(this._lose, this);
    this._outThresholdZone.events.onInputUp.remove(this._lose, this);
    this._innerThresholdZone.events.onInputOver.remove(this._lose, this);
    this._deadZone.events.onInputOver.remove(this._lose, this);

    this._hitAreaInnerBoundaries.events.onInputOver.remove(this._minorFail, this);
    this._outThresholdZone.events.onInputOver.remove(this._minorFail, this);
  }

  _addListeners() {
    this._outThresholdZone.name = 'puzzle';
    this._outThresholdZone.inputEnabled = true;
    this._outThresholdZone.input.priorityID = InputPriority.Game + 1;

    this._hitArea.name = 'puzzle';
    this._hitArea.inputEnabled = true;
    this._hitArea.input.priorityID = InputPriority.Game + 2;

    this._hitAreaInnerBoundaries.name = 'background';
    this._hitAreaInnerBoundaries.inputEnabled = true;
    this._hitAreaInnerBoundaries.input.priorityID = InputPriority.Game + 3;

    this._innerThresholdZone.name = 'background';
    this._innerThresholdZone.inputEnabled = true;
    this._innerThresholdZone.input.priorityID = InputPriority.Game + 4;

    this._deadZone.name = 'puzzle';
    this._deadZone.inputEnabled = true;
    this._deadZone.input.priorityID = InputPriority.Game;

    this._hitArea.events.onInputDown.addOnce(this._onDown, this);
  }

  _lose() {
    lego.event.emit(ViewEvents.BoardView.Fail);
  }

  _minorFail() {
    lego.event.emit(ViewEvents.BoardView.MinorFail);
  }

  _playLoseFX() {
    if (!this._drawnPoints.length) {
      return;
    }

    if (!this._loseFxRunnable) {
      const pointsOptimalCount = Math.max(Math.min(150, this._drawnPoints.length * 0.5), 10);
      while (this._drawnPoints.length >= pointsOptimalCount) {
        this._drawnPoints = this._drawnPoints.filter((point, index) => index % 2 === 0);
      }

      this._loseFxRunnable = loopRunnable(0, () => {
        this._drawLine.clear();
        this._drawLine.lineStyle(CI_API.Globals.PARAMS.line_width, 0x526a8e, 1);
        this._drawnPoints.splice(-1, 1);

        this._drawnPoints.forEach((point, i) => {
          const { x, y } = point;

          if (i === 0) {
            this._drawLine.moveTo(x, y);
          } else {
            this._drawLine.lineTo(x, y);
          }
        });

        if (!this._drawnPoints.length) {
          removeRunnable(this._loseFxRunnable);
          this._loseFxRunnable = null;
        }
      });
    }
  }

  _onDown(event, pointer) {
    this._buildPointerIndicator();

    this._hitArea.events.onInputUp.add(this._lose, this);
    this._innerThresholdZone.events.onInputUp.add(this._lose, this);
    this._outThresholdZone.events.onInputUp.add(this._lose, this);

    this._innerThresholdZone.events.onInputOver.add(this._lose, this);
    this._deadZone.events.onInputOver.add(this._lose, this);

    this._hitAreaInnerBoundaries.events.onInputOver.add(this._minorFail, this);

    this._outThresholdZone.events.onInputOver.add(this._minorFail, this);

    const { worldX, worldY } = pointer;
    const { x, y } = this.toLocal(new Phaser.Point(worldX, worldY), null);
    this._drawLine.moveTo(x, y);
    this._drawnPoints.length = 0;
    this._drawnPoints.push(new Phaser.Point(x, y));
    this.game.input.addMoveCallback(this._onMove, this);

    this._pointerIndicator.position.set(x, y);
    this._checkForCheckpointsIntersection(
      this._checkpoints,
      ViewEvents.BoardView.CheckpointIntersection,
      ViewEvents.BoardView.NoCheckpointIntersection
    );
    this._checkForCheckpointsIntersection(
      this._keyCheckpoints,
      ViewEvents.BoardView.KeyCheckpointIntersection,
      ViewEvents.BoardView.NoKeyCheckpointIntersection
    );

    lego.event.emit(ViewEvents.BoardView.DrawStart);
  }

  _onMove(pointer) {
    lego.event.emit(ViewEvents.BoardView.Scratching);
    const { worldX, worldY } = pointer;
    const { x, y } = this.toLocal(new Phaser.Point(worldX, worldY), null);
    this._drawLine.lineTo(x, y);
    this._drawnPoints.push(new Phaser.Point(x, y));

    this._pointerIndicator.position.set(x, y);
    this._checkForCheckpointsIntersection(
      this._checkpoints,
      ViewEvents.BoardView.CheckpointIntersection,
      ViewEvents.BoardView.NoCheckpointIntersection
    );
    this._checkForCheckpointsIntersection(
      this._keyCheckpoints,
      ViewEvents.BoardView.KeyCheckpointIntersection,
      ViewEvents.BoardView.NoKeyCheckpointIntersection
    );
  }

  _checkForCheckpointsIntersection(points, intersectionEvent, noIntersectionEvent) {
    for (let i = 0; i < points.length; i += 1) {
      const checkpoint = points[i];

      const { uuid } = checkpoint;
      const { shape } = store.game.boardModel.getPointByUuid(uuid);
      const chBounds = checkpoint.getBounds();
      const indicatorBounds = this._pointerIndicator.getBounds();
      let isIntersect;

      if (shape === 'rect') {
        // @ts-ignore
        isIntersect = Phaser.Rectangle.intersects(chBounds, indicatorBounds);
      } else if (shape === 'circle') {
        const { x, y, width } = chBounds;

        isIntersect = Phaser.Circle.intersectsRectangle(
          new Phaser.Circle(x + width / 2, y + width / 2, width),
          // @ts-ignore
          indicatorBounds
        );
      }

      if (isIntersect) {
        lego.event.emit(intersectionEvent, uuid);
        return;
      }
    }

    lego.event.emit(noIntersectionEvent);
  }

  _checkForLinesIntersection() {
    const points = this._drawnPoints.slice(0, Math.max(this._drawnPoints.length - 5, 0));

    if (points.length > 30) {
      return points.find((point, i) => {
        if (i > 0 && i < points.length - 2) {
          const { x: x1, y: y1 } = point;
          const { x: x0, y: y0 } = points[i - 1];

          const { x: x2, y: y2 } = this._drawnPoints[this._drawnPoints.length - 2];
          const { x: x3, y: y3 } = this._drawnPoints[this._drawnPoints.length - 1];

          return lineSegmentsIntersect(x0, y0, x1, y1, x2, y2, x3, y3);
        }

        return false;
      });
    }

    return false;
  }

  _build() {
    this._buildBg();

    const { keyCheckpoints, checkpoints } = store.game.boardModel;

    this._checkpoints = this._buildCheckpoints(checkpoints);
    this._keyCheckpoints = this._buildCheckpoints(keyCheckpoints);
    this._buildDrawLineGraphic();
  }

  _buildPointerIndicator() {
    this._pointerIndicator = makePixel({ width: 1, height: 1, alpha: 0, tint: 0xff0000 });
    this._pointerIndicator.anchor.set(0.5);
    this.addChild(this._pointerIndicator);
  }

  _buildBg() {
    const img = makeImage(getLevelShapeImageConfig());
    img.anchor.set(0);
    img.alpha = 0.4;
    this.addChild((this._bg = img));
  }

  _buildDrawLineGraphic() {
    const gr = this.game.add.graphics();
    gr.lineStyle(CI_API.Globals.PARAMS.line_width, 0x526a8e, 1);
    this.addChild((this._drawLine = gr));
  }

  _buildHitArea(config, color) {
    const gr = this.game.add.graphics();
    gr.beginFill(color, 0);
    this.addChild(gr);

    const { rects, triangles, arcs } = config;

    rects.forEach(lineConfig => {
      const { x, y, width, height } = lineConfig;
      gr.drawRect(x, y, width, height);
    });

    triangles.forEach(points => {
      gr.drawTriangle(points);
    });

    arcs.forEach(arcConfig => {
      const { cx, cy, radius, startAngle, endAngle } = arcConfig;
      gr.arc(cx, cy, radius, startAngle, endAngle, false);
    });

    return gr;
  }

  _buildCheckpoints(checkpoints) {
    return checkpoints.map(checkpointModel => {
      const { uuid, config } = checkpointModel;
      const { x, y } = config;
      const checkpoint = new CheckpointComponent(uuid, checkpointModel.config);
      checkpoint.position.set(x, y);
      this.addChild(checkpoint);

      return checkpoint;
    });
  }
}
