/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { InputPriority, SoundState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { Container } from '../../utils/container';

export class SoundView extends Container {
  constructor() {
    super();

    this._onBtn = null;
    this._offBtn = null;

    this._build();

    lego.event.on(ModelEvents.SoundModel.StateUpdate, this._onSoundStateUpdate, this);
  }

  _onSoundStateUpdate(value) {
    switch (value) {
      case SoundState.On:
        this._onBtn.visible = true;
        this._offBtn.visible = false;
        this.game.sound.volume = 1;
        break;
      case SoundState.Off:
        this._onBtn.visible = false;
        this._offBtn.visible = true;
        this.game.sound.volume = 0;
        break;
      default:
    }
  }

  _build() {
    this._onBtn = imageLoader.button(0, 0, 'ui/sound_on.png', this._off, this);
    this._offBtn = imageLoader.button(0, 0, 'ui/sound_off.png', this._on, this);

    this._onBtn.name = 'mute_button_on';
    this._offBtn.name = 'mute_button_off';

    this._onBtn.input.priorityID = InputPriority.Foreground;
    this._offBtn.input.priorityID = InputPriority.Foreground;

    this.addMultiple([this._onBtn, this._offBtn]);
  }

  _on() {
    lego.event.emit(ViewEvents.SoundView.Click, 'on');
  }

  _off() {
    lego.event.emit(ViewEvents.SoundView.Click, 'off');
  }
}
