/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getForegroundGridConfig } from '../../configs/grid-configs';
import { AdStatus, GameOverReasons } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import Globals from '../../kernel/globals';
import { store } from '../../models/store';
import { tweenToCell } from '../../utils';
import { HintView } from './hint-view';
import { IntroView } from './intro-view';
import { LogoView } from './logo-view';
import { SoundView } from './sound-view';
import { TutorialView } from './tutorial-view';

export class ForegroundView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    lego.event
      .on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this)
      .on(ModelEvents.SoundModel.IconUpdate, this._onSoundIconUpdate, this)
      .on(ModelEvents.AdModel.HintUpdate, this._onHintUpdate, this)
      .on(ModelEvents.AdModel.TutorialUpdate, this._onTutorialUpdate, this)
      .on(ModelEvents.AdModel.IntroUpdate, this._onIntroUpdate, this);
  }

  get name() {
    return 'ForegroundView';
  }

  getGridConfig() {
    return getForegroundGridConfig();
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Game:
        this._build();
        break;
      case AdStatus.Cta:
        if (CI_API.Globals.PARAMS.cta_type === 'embedded') {
          this.game.tweens.removeFrom(this._logoView);
          this.game.tweens.removeFrom(this._logoView.scale);
          tweenToCell(this, this._logoView, 'logo', 800, Phaser.Easing.Cubic.InOut, 0);
        }
        break;
      default:
    }
  }

  _onSoundIconUpdate(value) {
    value && this._buildSoundIcon();
  }

  _build() {
    this._buildLogo();
  }

  // LOGO
  _buildLogo() {
    if (!Globals.PARAMS.tutorial && !Globals.PARAMS.intro) {
      this.setChild('center_logo', (this._logoView = new LogoView()));
    } else {
      this.setChild('logo', (this._logoView = new LogoView()));
    }
  }

  _moveLogo() {
    const delay = store.ad.cta.reason !== GameOverReasons.Idled ? 500 : 0;
    tweenToCell(this, this._logoView, 'center_logo', 800, Phaser.Easing.Cubic.InOut, delay);
  }

  // SOUND
  _buildSoundIcon() {
    this.setChild('sound', (this._soundView = new SoundView()));
  }

  // INTRO
  _onIntroUpdate(intro) {
    if (intro) {
      this._buildIntro();
    } else {
      if (!Globals.PARAMS.tutorial) {
        this._moveLogo();
      }
      this._destroyIntro();
    }
  }

  _buildIntro() {
    this._introView = new IntroView();
    this.setChild('intro', this._introView);
  }

  _destroyIntro() {
    if (store.ad.cta.reason !== GameOverReasons.Idled) {
      this._introView.hide().onComplete.add(() => {
        this._introView.destroy();
      });
    } else {
      this._introView.destroy();
    }
  }

  // TUTORIAL
  _onTutorialUpdate(tutorial) {
    if (tutorial) {
      this._buildTutorial();
    } else {
      this._moveLogo();

      this._destroyTutorial();
    }
  }

  _buildTutorial() {
    this._tutorialView = new TutorialView();
    this._tutorialView.onSequenceReady.add(this._onTutorialSequenceReady, this);
  }

  _onTutorialSequenceReady() {
    this.setChild('tutorial', this._tutorialView);
  }

  _destroyTutorial() {
    if (store.ad.cta.reason !== GameOverReasons.Idled) {
      this._tutorialView.hide().onComplete.add(() => {
        this._tutorialView.destroy();
      });
    } else {
      this._tutorialView.destroy();
    }
  }

  // HINT
  _onHintUpdate(hint) {
    hint ? this._buildHint() : this._destroyHint();
  }

  _buildHint() {
    this.addChild((this._hintView = new HintView()));
  }

  _destroyHint() {
    this._hintView.destroy();
  }
}
