import { lego } from '@armathai/lego';
import { ModelEvents } from '../../events/model-events';
import { adStatusUpdateCommand } from './ad-status-update-command';

export function mapAdStatusUpdateCommand() {
  lego.command
    .off(ModelEvents.AdModel.StatusUpdate, adStatusUpdateCommand)
    .on(ModelEvents.AdModel.StatusUpdate, adStatusUpdateCommand);
}
