import { lego } from '@armathai/lego';
import { AdViewState } from '../../constants';
import { gameInitCommand } from '../game-init-command';
import { gameShutDownCommand } from '../game-shut-down-command';
import { gameStartCommand } from '../game-start-command';

export function adViewStateUpdateCommand(state) {
  switch (state) {
    case AdViewState.Init:
      lego.command.execute(gameInitCommand);
      break;

    case AdViewState.Create:
      lego.command.execute(gameStartCommand);
      break;

    case AdViewState.ShutDown:
      lego.command.execute(gameShutDownCommand);
      break;
    default:
  }
}
