import { lego } from '@armathai/lego';
import { AdStatus } from '../../../constants';
import { ctaPreVisibleGuard } from '../../../guards/ad/cta-previsible-guard';
import { setAdStatusCommand } from '../set-ad-status-command';

export function ctaPreVisibleUpdateCommand() {
  lego.command
    .guard(ctaPreVisibleGuard)
    .payload(AdStatus.PreCta)
    .execute(setAdStatusCommand);
}
