/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getGameGridConfig } from '../../configs/grid-configs';
import { getRetryTextConfig } from '../../configs/text-configs';
import { BoardState, GameOverReasons, InputPriority } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { fitText, makePixel, makeText } from '../../utils';
import { BoardView } from './board/board-view';
import { ResultComponent } from './result-component';

const resultCircleConfig = {
  win: {
    color: 0x22a354,
    text: 'WIN'
  },
  fail: {
    color: 0xba535a,
    text: 'FAIL'
  }
};

export class GameView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    this._boardView = null;

    lego.event
      .on(ModelEvents.GameModel.BoardModelUpdate, this._onBoardUpdate, this)
      .on(ModelEvents.CtaModel.PreVisibleUpdate, this._onCtaPreVisibleUpdate, this)
      .on(ModelEvents.BoardModel.StateUpdate, this._onBoardStateUpdate, this);
  }

  get name() {
    return 'GameView';
  }

  getGridConfig() {
    return getGameGridConfig();
  }

  build(config) {
    super.build(config);
    this._buildBlocker(0);
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  _onCtaPreVisibleUpdate(value) {
    if (value) {
      if (this._result) {
        if (CI_API.Globals.PARAMS.cta_type === 'standard') {
          if (store.ad.cta.reason === GameOverReasons.Idled) {
            this.game.tweens.remove(this._result.hideTw);
            this.game.tweens.remove(this._result.showTw);
            this.game.add.tween(this._result.scale).to({ x: 1, y: 1 }, 400, Phaser.Easing.Back.Out, true);
          }
        } else if (store.ad.cta.reason === GameOverReasons.Idled) {
          this.game.tweens.remove(this._result.hideTw);
          this.game.tweens.remove(this._result.showTw);
          this.game.add
            .tween(this._result.scale)
            .to({ x: 0, y: 0 }, 400, Phaser.Easing.Linear.None, true)
            .onComplete.add(() => {
              this._startEmbeddedCtaAnimation();
            });
        }
      } else {
        this._showLoseResult();
      }
    }
  }

  _showBoxLabel() {
    this._boxLabel = makeText(getRetryTextConfig());
    this._boxLabel.alpha = 0;
    fitText(this._boxLabel, 450, 300);

    this.setChild('box_label', this._boxLabel);

    this.game.add
      .tween(this._boxLabel)
      .to({ alpha: 1 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 100)
      .onComplete.add(() => {
        this.game.add
          .tween(this._boxLabel)
          .to({ alpha: 0 }, 300, Phaser.Easing.Sinusoidal.InOut, true, 600)
          .onComplete.add(() => {
            this._boxLabel.destroy();
          });
      });
  }

  _showBoxBlocker() {
    this._boxBg = makePixel({ alpha: 0 });
    this.setChild('box_bg', this._boxBg);
    this.game.add
      .tween(this._boxBg)
      .to({ alpha: 0.5 }, 500, Phaser.Easing.Cubic.InOut, true, 100)
      .onComplete.add(() => {
        this.game.add
          .tween(this._boxBg)
          .to({ alpha: 0 }, 300, Phaser.Easing.Cubic.InOut, true, 600)
          .onComplete.add(() => {
            this._boxBg.destroy();
          });
      });
  }

  _onBoardStateUpdate(state) {
    switch (state) {
      case BoardState.Lose:
        this._showLoseResult();
        break;
      case BoardState.Restart:
        this._showRetryResult();
        break;
      case BoardState.Win:
        this._showWinResult();
        break;

      default:
        break;
    }
  }

  _showLoseResult() {
    this._buildResult('fail')
      .show()
      .onComplete.add(() => {
        if (CI_API.Globals.PARAMS.cta_type === 'embedded') {
          this._startEmbeddedCtaAnimation();
        }
      });
  }

  _showRetryResult() {
    this._buildResult('fail');
    this._showBoxBlocker();
    this._showBoxLabel();
    this._result.bounceHideCompleteSignal.addOnce(() => {
      this._destroyResult();
    });
    this._result.bounce();
  }

  _showWinResult() {
    this._buildResult('win')
      .show()
      .onComplete.add(() => {
        if (CI_API.Globals.PARAMS.cta_type === 'embedded') {
          this._startEmbeddedCtaAnimation();
        }
      });
  }

  _startEmbeddedCtaAnimation() {
    this._result.hide().onComplete.add(() => {
      this._destroyResult();
      lego.event.emit(ViewEvents.GameView.ResultHideComplete);

      this._boardView.hide().onComplete.add(() => {
        this._destroyBoard();
      });
    });
  }

  // BOARD
  _onBoardUpdate(board) {
    board && this._buildBoard();
  }

  _buildBoard() {
    this.setChild('board', (this._boardView = new BoardView()));
  }

  _destroyBoard() {
    this._boardView.destroy();
    this._boardView = null;
  }

  _buildBlocker(alpha) {
    this._blocker = makePixel({ alpha });
    this._blocker.inputEnabled = true;
    this._blocker.name = 'background';
    this._blocker.input.priorityID = InputPriority.Game;
    this.setChild('blocker', this._blocker);
  }

  _buildResult(result) {
    const { color, text } = resultCircleConfig[result];

    this._result = new ResultComponent(color, text);
    this._result.angle = -30;

    this.setChild('result', this._result);
    return this._result;
  }

  _destroyResult() {
    this._result.destroy();
    this._result = null;
  }
}
