/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getIntroTextConfig } from '../../configs/text-configs';
import { ViewEvents } from '../../events/view-events';
import Globals from '../../kernel/globals';
import { makeText } from '../../utils';
import { Container } from '../../utils/container';

export class IntroView extends Container {
  constructor() {
    super();

    this._build();
    this.game.input.onDown.addOnce(this._onScreenClick, this);
  }

  destroy() {
    this.game.input.onDown.remove(this._onScreenClick, this);

    super.destroy();
  }

  hide() {
    const tw = this.game.add.tween(this).to({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true);
    tw.universal = true;

    return tw;
  }

  _build() {
    this._buildLabel();
  }

  _buildLabel() {
    this._label = makeText(getIntroTextConfig(Globals.PARAMS.intro_txt));
    this.addChild(this._label);
  }

  _onScreenClick() {
    lego.event.emit(ViewEvents.IntroView.ScreenClick);
  }
}
