import { InputPriority } from '../constants';
import { getPCtaButtonImageConfig } from './image-configs';
import { getButtonPatchConfig, getPCTAButtonPatchConfig } from './ninepatch-configs';
import { getCtaPlayButtonTextConfig, getPersistentCtaTextConfig } from './text-configs';

// PERSISTENT CTA
export function getPersistentCtaButtonConfig() {
  return {
    input: {
      name: 'persistent_cta_button',
      priority: InputPriority.Game
    },
    states: {
      up: {
        bg: getPCTAButtonPatchConfig(),
        label: getPersistentCtaTextConfig()
      }
    }
  };
}

export function getPersistentCtaArrowButtonConfig() {
  return {
    input: {
      name: 'persistent_cta_button',
      priority: InputPriority.Game
    },
    states: {
      up: {
        bg: getPCtaButtonImageConfig()
      }
    }
  };
}

// CTA
export function getCtaPlayButtonConfig(text) {
  return {
    input: {
      name: 'cta_play_button',
      priority: InputPriority.Cta + 1
    },
    states: {
      up: {
        bg: getButtonPatchConfig(text),
        label: getCtaPlayButtonTextConfig(text)
      }
    }
  };
}
