import { ObservableModel } from '../../observable-model';

export class CheckpointModel extends ObservableModel {
  constructor(config) {
    super('CheckpointModel');

    const { shape = 'rect', keyPoint = false, intersectionsCount = 1 } = config;

    this._shape = shape;
    this._keyPoint = keyPoint;
    this._config = config;
    this._intersectionsCount = intersectionsCount;
    this._config.shape = shape;

    this.makeObservable('_intersectionsCount');
  }

  get shape() {
    return this._shape;
  }

  get keyPoint() {
    return this._keyPoint;
  }

  get config() {
    return this._config;
  }

  set config(value) {
    this._config = value;
  }

  get checked() {
    return !this._intersectionsCount;
  }

  get intersectionsCount() {
    return this._intersectionsCount;
  }

  set intersectionsCount(value) {
    this._intersectionsCount = value;
  }

  reset() {
    const { intersectionsCount = 1 } = this._config;
    this._intersectionsCount = intersectionsCount;
  }
}
