/* eslint-disable no-nested-ternary */
import { CellAlign } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getTutorialSequenceGridLandscapeConfig() {
  const { intro } = CI_API.Globals.PARAMS;
  const sequenceY = intro ? 0.13 : 0;
  const sequenceH = intro ? 0.1 : 0.275;
  const align = intro ? CellAlign.CenterTop : CellAlign.Center;

  return {
    // debug: { color: 0xd95027 },
    name: 'tutorial_sequence',
    cells: [
      {
        name: '0',
        align,
        bounds: { x: 0, y: sequenceY, width: 1, height: sequenceH },
        padding: { x: 0.325, y: 0, width: 0.35, height: 1 }
      }
    ]
  };
}

export function getTutorialSequenceGridPortraitConfig() {
  const { intro } = CI_API.Globals.PARAMS;
  const sequenceY = intro ? 0.2 : 0.15;
  const sequenceH = intro ? 0.1 : isSquareLikeScreen() ? 0.15 : 0.2;
  const paddingX = intro ? 0.15 : 0.1;
  const paddingW = intro ? 0.7 : 0.8;
  const align = intro ? CellAlign.CenterTop : CellAlign.Center;

  return {
    // debug: { color: 0xd95027 },
    name: 'tutorial_sequence',
    cells: [
      {
        name: '0',
        align,
        padding: { x: paddingX, width: paddingW },
        bounds: { x: 0, y: sequenceY, width: 1, height: sequenceH }
      }
    ]
  };
}
