import { PhaserState } from '../../constants';
import { GameState } from '../../states/game-state';
import { PreloaderState } from '../../states/preloader-state';

export function initializePhaserStatesCommand() {
  const { game, states } = CI_API;
  states.preloader = CI_API.game.state.add(PhaserState.Preload, PreloaderState, false);
  states.game = CI_API.game.state.add(PhaserState.Game, GameState, false);
  game.state.start(PhaserState.Preload);
}
