import { startupCommand } from './commands/startup-command';
import FontLoader from './display/font-loader';
import LU from './display/layout-utils';
import Globals from './kernel/globals';
import './lib/particle-storm';
import { CTAView } from './objects/cta/cta-view';
import { GameState } from './states/game-state';
import { PreloaderState } from './states/preloader-state';
import './strings';

window.CI_API = window.CI_API || {
  states: {},
  PreloaderState,
  GameState,
  FontLoader,
  LU,
  CTA: null, // define CTA later
  Globals,
  renderer: 1 /* 0 - Auto | << 1 - Canvas >> | 2 - WebGL */
};

class Game extends Phaser.Game {
  constructor() {
    super('100%', '100%', CI_API.renderer, LU.CONTAINER_NAME, null);
  }
}

/* Load our font resources and create a new game */
window.ad_begin = () => {
  FontLoader.loadFonts(() => {
    /**
     * Create new game after initializing all information about loading modules.
     * xp-assets.json is loaded as independent file in the -preview mode.
     * May define the CTA class here as a CTA xp module.
     */
    xp.init(Globals.WEB_ROOT, () => {
      CI_API.CTA = xp.getActiveModule('cta_style', CTAView);
      // @ts-ignore
      CI_API.game = new Game();
      setTimeout(() => {
        // legologger.start(legoLoggerConfig);
        startupCommand();
      });
    });
  });
};
