import { AdModel } from './ad/ad-model';
import { GameModel } from './game/game-model';
import { ObservableModel } from './observable-model';
import { PlayerModel } from './player/player-model';

class Store extends ObservableModel {
  constructor() {
    super('Store');

    this._ad = null;
    this._game = null;
    this._player = null;

    this.makeObservable();
  }

  get ad() {
    return this._ad;
  }

  set ad(value) {
    this._ad = value;
  }

  get player() {
    return this._player;
  }

  set player(value) {
    this._player = value;
  }

  get game() {
    return this._game;
  }

  set game(value) {
    this._game = value;
  }

  // AD
  initializeADModel() {
    this._ad = new AdModel();
    this._ad.initialize();
  }

  // PLAYER
  initializePlayerModel() {
    this._player = new PlayerModel();
    this._player.initialize();
  }

  destroyPlayerModel() {
    this._player.destroy();
    this._player = null;
  }

  // GAME
  initializeGameModel() {
    this._game = new GameModel();
    this._game.initialize();
  }

  destroyGameModel() {
    this._game.destroy();
    this._game = null;
  }
}

export const store = new Store();
