import { ObservableModel } from '../observable-model';

export class LoadModel extends ObservableModel {
  constructor() {
    super('LoadModel');

    this._progress = 0;
    this._complete = false;
    this.makeObservable();
  }

  get progress() {
    return this._progress;
  }

  set progress(value) {
    this._progress = value;
  }

  get complete() {
    return this._complete;
  }

  set complete(value) {
    this._complete = value;
  }
}
