export const tweenTint = ({
  target,
  to,
  from,
  duration = 500,
  easing = Phaser.Easing.Cubic.InOut,
  delay = 0,
  repeat = 0,
  yoyo = false
}) => {
  const colorTo = Phaser.Color.getRGB(to);
  const colorFrom = Phaser.Color.getRGB(from);
  const tween = CI_API.game.add.tween(colorFrom).to(colorTo, duration, easing, true, delay, repeat, yoyo);

  tween.onStart.addOnce(() => {
    target.tint = from;
  });

  tween.onUpdateCallback(() => {
    Phaser.Color.updateColor(tween.target);
    target.tint = Phaser.Color.getColor32(target.alpha, tween.target.r, tween.target.g, tween.target.b);
  }, this);

  tween.onComplete.addOnce(() => {
    target.tint = to;
  });

  return tween;
};

export function handScaleTw(hand, amplitude = 0.2, yoyo = false) {
  const signX = Phaser.Math.sign(hand.scale.x);
  const signY = Phaser.Math.sign(hand.scale.y);
  const scaleTw = CI_API.game.add.tween(hand.scale).to(
    {
      x: hand.scale.x - amplitude * signX,
      y: hand.scale.y - amplitude * signY
    },
    500,
    Phaser.Easing.Cubic.InOut,
    true,
    0,
    0,
    yoyo
  );

  return scaleTw;
}

export function handMoveTw(hand, poses) {
  const xs = poses.map(pos => pos.x);
  const ys = poses.map(pos => pos.y);

  const moveTw = CI_API.game.add.tween(hand).to({ x: xs, y: ys }, 6000, Phaser.Easing.Linear.None, true, 0, 0, false);

  return moveTw;
}

export function lineSegmentsIntersect(x1, y1, x2, y2, x3, y3, x4, y4) {
  const aDx = x2 - x1;
  const aDy = y2 - y1;
  const bDx = x4 - x3;
  const bDy = y4 - y3;
  const s = (-aDy * (x1 - x3) + aDx * (y1 - y3)) / (-bDx * aDy + aDx * bDy);
  const t = (+bDx * (y1 - y3) - bDy * (x1 - x3)) / (-bDx * aDy + aDx * bDy);

  return s >= 0 && s <= 1 && t >= 0 && t <= 1 ? [x1 + t * aDx, y1 + t * aDy] : false;
}
