/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getPersistentCtaArrowButtonConfig, getPersistentCtaButtonConfig } from '../../configs/button-configs';
import { getSparkleEmitterConfig } from '../../configs/particles-configs';
import { ViewEvents } from '../../events/view-events';
import Globals from '../../kernel/globals';
import { makeEmitter, pulse } from '../../utils';
import { Button } from '../../utils/button/button';
import { Container } from '../../utils/container';

export class PersistentCTAView extends Container {
  constructor() {
    super();
    this._build();
  }

  getBounds() {
    const x = LP(-65, -237);
    const y = LP(-55, -58);
    const width = LP(129, 475);
    const height = LP(110, 115);
    return new Phaser.Rectangle(x, y, width, height);
  }

  destroy() {
    if (this._sparkleEmitter) {
      this._sparkleEmitter.paused = true;
    }
    super.destroy();
  }

  _build() {
    this._buildButton();
    this._buildPersistentCtaEffect();
  }

  _buildPersistentCtaEffect() {
    const effect = Globals.PARAMS.pcta_effects;
    switch (effect) {
      case 'sparkle':
        this._buildSparkle();
        break;
      case 'pulse':
        pulse(this);
        break;
      case 'both':
        this._buildSparkle();
        pulse(this);
        break;
      default:
        break;
    }
  }

  _buildButton() {
    const config = LP(getPersistentCtaArrowButtonConfig(), getPersistentCtaButtonConfig());

    this._button = new Button(config);
    this._button.onClick.add(() => lego.event.emit(ViewEvents.PersistentCtaView.Click), this);
    this.addChild(this._button);
  }

  _buildSparkle() {
    const { x, y, width, height } = this;
    this._sparkleEmitter = makeEmitter(getSparkleEmitterConfig(this, { x, y, width, height }));
    this.bringToTop(this._button);
  }
}
