import { particles } from '../../configs/particles-configs';

const addData = config => {
  const { key, data } = config;
  CI_API.game.particleStorm.addData(key, data);
};

export function initializeParticlesCommand() {
  const { game } = CI_API;
  game.particleStorm = game.plugins.add(Phaser.ParticleStorm);
  particles.forEach(p => addData(p));
}
