/* eslint-disable class-methods-use-this */
import { makePixel } from '../../../utils';
import { Container } from '../../../utils/container';

export class CheckpointComponent extends Container {
  constructor(uuid, config) {
    super();

    this._uuid = uuid;
    this._config = config;

    this._build();
  }

  get uuid() {
    return this._uuid;
  }

  get name() {
    return 'CheckpointComponent';
  }

  get checkpointArea() {
    return this._hitArea;
  }

  setChecked(count) {
    if (!count) {
      this._hitArea.tint = 0xff0000;
    } else {
      this._hitArea.tint = 0x000000;
    }
  }

  _build() {
    const { angle = 0, shape, width = 4, height = 90, diameter = 60 } = this._config;

    let hitArea;

    if (shape === 'rect') {
      hitArea = makePixel({ width, height, alpha: 1 });
      hitArea.anchor.set(0.5);
      hitArea.angle = angle;
    } else if (shape === 'circle') {
      hitArea = this.game.add
        .graphics()
        .beginFill(0xffffff, 1)
        .drawCircle(0, 0, diameter)
        .endFill();
      hitArea.tint = 0x000000;
    }

    hitArea.alpha = 0;

    this.addChild((this._hitArea = hitArea));
  }
}
