import { lego, not } from '@armathai/lego';
import { lastTutorialSequenceGuard } from '../../../guards/ad/tutorial-last-sequence-guard';
import { tutorialModelGuard } from '../../../guards/ad/tutorial-model-guard';
import { nextTutorialSequenceCommand } from './next-tutorial-sequence-command';
import { setTutorialCompleteCommand } from './set-tutorial-complete-command';
import { showTutorialSequenceCommand } from './show-tutorial-sequence-command';

export function tutorialSequenceViewCompleteCommand() {
  lego.command
    //
    .guard(tutorialModelGuard, lastTutorialSequenceGuard)
    .execute(setTutorialCompleteCommand)

    .guard(tutorialModelGuard, not(lastTutorialSequenceGuard))
    .execute(nextTutorialSequenceCommand, showTutorialSequenceCommand);
}
