/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getTutorialGridConfig } from '../../configs/grid-configs';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { postRunnable } from '../../utils';
import { TutorialSequenceView } from './tutorial-sequence-view';

export class TutorialView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    this._current = null;
    this.onSequenceReady = new Phaser.Signal();

    lego.event.on(ModelEvents.TutorialSequenceModel.ShowUpdate, this._onTutorialSequenceShowUpdate, this);
    lego.event.on(ModelEvents.TutorialSequenceModel.CompleteUpdate, this._onTutorialCurrentCompleteUpdate, this);
  }

  getGridConfig() {
    return getTutorialGridConfig();
  }

  destroy(destroyChildren = true, soft = false) {
    this._switchScreenInput(false);

    super.destroy(destroyChildren, soft);
  }

  hide() {
    const tw = this.game.add.tween(this).to({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true);
    tw.universal = true;

    return tw;
  }

  _onTutorialSequenceShowUpdate() {
    const { current: sequence } = store.ad.tutorial;
    if (!sequence) {
      return;
    }

    this._buildSequence(sequence, false);
    this.onSequenceReady.dispatch();
  }

  _onTutorialCurrentCompleteUpdate(complete) {
    if (!complete) {
      return;
    }

    this._switchScreenInput(false);
    this._current.hide().onComplete.addOnce(() => {
      this._current.destroy();
      this._current = null;
      lego.event.emit(ViewEvents.TutorialView.SequenceHideComplete);
    });
  }

  _buildSequence(sequence, force) {
    this._current = new TutorialSequenceView(sequence.config, sequence.index);
    this.setChild('sequence', this._current);

    if (!force) {
      postRunnable(() => {
        this._current.show().onComplete.addOnce(() => {
          const { clickToComplete } = store.ad.tutorial.current.config;
          this._switchScreenInput(clickToComplete);
        });
      });
    }
  }

  _switchScreenInput(enable) {
    this.game.input.onDown.remove(this._onScreenClick, this);

    enable && this.game.input.onDown.addOnce(this._onScreenClick, this);
  }

  _onScreenClick() {
    lego.event.emit(ViewEvents.TutorialView.ScreenClick);
  }
}
