export class UniversalTween extends Phaser.Tween {
  constructor(target, startValues, endValues, context) {
    super(target, CI_API.game, CI_API.game.tweens);
    this.__context__ = context;
    this.getStartValues = startValues;
    this.getEndValues = endValues;
  }

  getPropertiesStartValue() {
    super.start();
    return this.getStartValues.call(this.__context__);
  }

  getPropertiesEndValue() {
    return this.getEndValues.call(this.__context__);
  }
}
