import { SoundState } from '../../constants';
import { ObservableModel } from '../observable-model';

export class SoundModel extends ObservableModel {
  constructor() {
    super('SoundModel');

    this._state = SoundState.Unknown;
    this._icon = null;

    this.makeObservable();
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  get icon() {
    return this._icon;
  }

  set icon(value) {
    this._icon = value;
  }

  initialize() {
    this._icon = CI_API.Globals.PARAMS.sound_icon;
    this._state = SoundState.On;
  }
}
