import { lego } from '@armathai/lego';
import { AdStatus } from '../constants';
import { introModelGuard } from '../guards/ad/intro-model-guard';
import { tutorialModelGuard } from '../guards/ad/tutorial-model-guard';
import { setAdStatusCommand } from './ad/set-ad-status-command';
import { setLastInteractionCommand } from './ad/set-last-interaction-command';
import { setIntroSkipCommand } from './ad/tutorial/set-intro-skip-command';
import { setTutorialSkipCommand } from './ad/tutorial/set-tutorial-skip-command';
import { unmapPlayableCommandsCommand } from './game/unmap-playable-commands-command';
import { shutdownModelsCommand } from './shutdown-models-command';

export function gameShutDownCommand() {
  lego.command
    .guard(tutorialModelGuard)
    .execute(setTutorialSkipCommand)

    .guard(introModelGuard)
    .execute(setIntroSkipCommand)

    .execute(unmapPlayableCommandsCommand)

    .execute(shutdownModelsCommand)

    .payload(0)
    .execute(setLastInteractionCommand)

    .payload(AdStatus.Unknown)
    .execute(setAdStatusCommand);
}
