import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';

export class Container extends Phaser.Group {
  constructor() {
    super(CI_API.game);
  }

  destroy(destroyChildren = true, soft = false) {
    lego.event.removeListenersOf(this);

    super.destroy(destroyChildren, soft);
  }
}

Phaser2Grid.prototype.destroy = Container.prototype.destroy;
