import { NinePatchPlugin } from '@armathai/phaser2-ninepatch';
import { ninePatches } from '../../configs/ninepatch-configs';

const addNinePatch = config => {
  const { key, data } = config;
  CI_API.game.cache.addNinePatch(key, data);
};

export function initializeNinePatchesCommand() {
  const { game } = CI_API;
  game.plugins.add(NinePatchPlugin);
  ninePatches.forEach(ninePatch => addNinePatch(ninePatch));
}
