import { lego, not } from '@armathai/lego';
import { AdStatus } from '../../../constants';
import { adLiveGuard } from '../../../guards/ad/ad-live-guard';
import { asecGuard } from '../../../guards/ad/asec-guard';
import { ctaVisibleGuard } from '../../../guards/ad/cta-visible-guard';
import { setAdStatusCommand } from '../set-ad-status-command';

export function ctaVisibleUpdateCommand() {
  lego.command
    .guard(ctaVisibleGuard, adLiveGuard, not(asecGuard))
    .payload(AdStatus.Cta)
    .execute(setAdStatusCommand)

    .guard(ctaVisibleGuard, not(adLiveGuard))
    .payload(AdStatus.Restart)
    .execute(setAdStatusCommand);
}
