import { getTutorialConfig } from '../../configs/tutorial-config';
import { delayRunnable, removeRunnable } from '../../utils';
import { ObservableModel } from '../observable-model';
import { TutorialSequenceModel } from './tutorial-sequence-model';

export class TutorialModel extends ObservableModel {
  constructor() {
    super('TutorialModel');

    this._complete = false;
    this._skip = false;
    this._sequences = null;
    this._currentIndex = null;
    this._sequenceCompleteTimer = null;

    this.makeObservable('_complete', '_skip');
  }

  get complete() {
    return this._complete;
  }

  set complete(value) {
    this._complete = value;
  }

  get skip() {
    return this._skip;
  }

  set skip(value) {
    this._skip = value;
  }

  get sequences() {
    return this._sequences;
  }

  get current() {
    return this._sequences[this._currentIndex];
  }

  get currentIndex() {
    return this._currentIndex;
  }

  getSequenceByUuid(uuid) {
    return this._sequences.find(sequence => sequence.uuid === uuid);
  }

  initialize() {
    super.initialize();

    this._initSequences();

    this.nextSequence();
    this.showSequence();
  }

  destroy() {
    this._stopSequenceCompleteTimer();
    super.destroy();
  }

  nextSequence() {
    this.current && this.completeSequence();
    this._currentIndex = this.current ? this._currentIndex + 1 : 0;
  }

  showSequence() {
    this.current.show = true;
    this._startSequenceCompleteTimer();
  }

  completeSequence() {
    this.current.complete = true;
    this._stopSequenceCompleteTimer();
  }

  _initSequences() {
    this._sequences = getTutorialConfig().map((config, index) => new TutorialSequenceModel(config, index));
  }

  _startSequenceCompleteTimer() {
    const { duration } = this.current.config;

    if (duration !== -1) {
      this._sequenceCompleteTimer = delayRunnable(duration, this.completeSequence, this);
    }
  }

  _stopSequenceCompleteTimer() {
    removeRunnable(this._sequenceCompleteTimer);
  }
}
