import { FONT1 } from '../constants';

// TUTORIAL
export function getTutorialTextConfig(text) {
  return {
    text,
    style: {
      font: FONT1,
      fontSize: 56,
      fill: '#546a91',
      align: 'center',
      wordWrap: true,
      wordWrapWidth: 480
    }
  };
}

