/* eslint-disable class-methods-use-this */
import { InputHandler } from './input-handler';

export class AbstractButton extends InputHandler {
  constructor({ states, input }) {
    super();

    this.onClick = new Phaser.Signal();
    this.states = this.createStates(states);
    this.inputArea = this.createHitArea(input);

    this.switchEnable(true);
  }

  switchEnable(value) {
    super.switchInput(value);

    this.setState('up');

    if (value === false) {
      this.setState('disable');
    }
  }

  // override
  onDown(_target, _pointer) {
    this.setState('down');
  }

  // override
  onUp(target, pointer, isOver) {
    this.setState('up');

    if (isOver) {
      this.onClick.dispatch(this);
    }
  }

  createStates({ up = null, down = null, disable = null } = {}) {
    return {
      up: up && this.createState(up),
      down: down && this.createState(down),
      disable: disable && this.createState(disable)
    };
  }

  // abstract
  createState(_config) {
    throw new Error('createState abstract function is not implemented');
  }

  setState(key) {
    if (!this.states[key]) {
      return;
    }
    Object.keys(this.states).forEach(prop => {
      const state = this.states[prop];
      if (!state) {
        return;
      }
      state.visible = false;
    });

    this.states[key].visible = true;
  }
}
