import { AdStatus, AdViewState } from '../../constants';
import Globals from '../../kernel/globals';
import { delayRunnable } from '../../utils';
import { ObservableModel } from '../observable-model';
import { CtaModel } from './cta-model';
import { HintModel } from './hint-model';
import { IntroModel } from './intro-model';
import { LoadModel } from './load-model';
import { PersistentCtaModel } from './persistent-cta-model';
import { SoundModel } from './sound-model';
import { TutorialModel } from './tutorial-model';

export class AdModel extends ObservableModel {
  constructor() {
    super('AdModel');

    this._cta = null;
    this._hint = null;
    this._load = null;
    this._sound = null;
    this._intro = null;
    this._tutorial = null;
    this._persistentCta = null;
    this._status = AdStatus.Unknown;
    this._viewState = AdViewState.Unknown;
    this._retries = 0; // CI_API.Globals.PARAMS.retries;

    this.makeObservable();
  }

  get status() {
    return this._status;
  }

  set status(value) {
    this._status = value;
  }

  get viewState() {
    return this._viewState;
  }

  set viewState(value) {
    this._viewState = value;
  }

  get load() {
    return this._load;
  }

  get hint() {
    return this._hint;
  }

  get tutorial() {
    return this._tutorial;
  }

  get intro() {
    return this._intro;
  }

  get persistentCta() {
    return this._persistentCta;
  }

  get cta() {
    return this._cta;
  }

  get sound() {
    return this._sound;
  }

  get retries() {
    return this._retries;
  }

  initialize() {
    this._load = new LoadModel();
  }

  // CTA
  initializeCtaModel() {
    this._cta = new CtaModel();
    this._cta.initialize();
  }

  destroyCtaModel() {
    this._cta.destroy();
    this._cta = null;
  }

  // HINT
  initializeHintModel() {
    this._hint = new HintModel();
    this._hint.initialize();
  }

  destroyHintModel() {
    this._hint.destroy();
    this._hint = null;
  }

  // LOAD
  initializeLoadModel() {
    this._load = new LoadModel();
    this._load.initialize();
  }

  destroyLoadModel() {
    this._load.destroy();
    this._load = null;
  }

  // INTRO
  initializeIntroModel() {
    this._intro = new IntroModel();
    this._intro.initialize();

    const params = Globals.PARAMS;

    delayRunnable(params.tutorial_time, () => {
      this._intro && this.destroyIntroModel();
    });
  }

  destroyIntroModel() {
    this._intro.destroy();
    this._intro = null;
  }

  // TUTORIAL
  initializeTutorialModel() {
    this._tutorial = new TutorialModel();
    this._tutorial.initialize();
  }

  destroyTutorialModel() {
    this._tutorial.destroy();
    this._tutorial = null;
  }

  // PERSISTENT_CTA
  initializePersistentCtaModel() {
    this._persistentCta = new PersistentCtaModel();
    this._persistentCta.initialize();
  }

  destroyPersistentCtaModel() {
    this._persistentCta.destroy();
    this._persistentCta = null;
  }

  // SOUND;
  initializeSoundModel() {
    this._sound = new SoundModel();
    this._sound.initialize();
  }

  destroySoundModel() {
    this._sound.destroy();
    this._sound = null;
  }

  decreaseRetriesCount() {
    this._retries -= 1;
  }
}
