import { lego } from '@armathai/lego';
import { GameOverReasons } from '../../constants';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { boardIdleStateGuard } from '../../guards/board/board-idle-state-guard';
import { loseStateGuard } from '../../guards/board/lose-state-guard';
import { restartStateGuard } from '../../guards/board/restart-state-guard';
import { winStateGuard } from '../../guards/board/win-state-guard';
import { showCtaCommand } from '../ad/cta/show-cta-command';
import { showHintCommand } from '../ad/hint/show-hint-command';
import { startHintVisibilityTimerCommand } from '../ad/hint/start-hint-visibility-timer-command';
import { resetPlayerLivesCommand } from '../player/reset-player-lives-command';
import { resetBoardCheckpointsCommand } from './reset-board-checkpoints-command';

export function onBoardStateUpdateCommand() {
  lego.command
    //
    .payload(GameOverReasons.ItemsToCtaReached, 0)
    .guard(winStateGuard)
    .execute(showCtaCommand)

    .payload(GameOverReasons.Failed, 0)
    .guard(loseStateGuard)
    .execute(showCtaCommand)

    .guard(restartStateGuard)
    .execute(resetPlayerLivesCommand, resetBoardCheckpointsCommand)

    .guard(boardIdleStateGuard, hintModelGuard)
    .execute(showHintCommand, startHintVisibilityTimerCommand);
}
