import { lego } from '@armathai/lego';
import { getHintPoints } from '../../configs/board-configs';
import { ModelEvents } from '../../events/model-events';
import { getViewByProperty, handMoveTw, handScaleTw, searchAtlasByFrame } from '../../utils';

export class HintView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/hand.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.anchor.set(0, 0);

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    this._hide();
  }

  destroy() {
    lego.event.off(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    this.game.tweens.remove(this._showTween);
    this.game.tweens.remove(this._moveTween);
    this.game.tweens.remove(this._scaleTween);

    super.destroy();
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  _show() {
    this._removeTw();
    this._positions = this._getHintPositions();
    this._currentPoint = 0;

    this._firstTimeShow();
  }

  _firstTimeShow() {
    const point = this._positions[this._currentPoint];
    this.scale.set(1.45);
    this.alpha = 1;
    this.position.set(point.x, point.y);
    this.angle = 0;
    this.visible = true;
    const { x, y, angle } = this;

    this._showTween = this.game.add
      .tween(this)
      .from({ x: x + 50, y: y + 50, angle: angle + 30 }, 400, Phaser.Easing.Cubic.Out, true, 0);
    this._showTween.onComplete.addOnce(() => {
      this._pointHand();
    });
    this.game.add.tween(this).from({ alpha: 0 }, 400, Phaser.Easing.Cubic.Out, true, 0);
  }

  _pointHand() {
    this._scaleTween = handScaleTw(this);
    this._scaleTween.onComplete.addOnce(() => {
      this._moveHand(this._positions);
    });
  }

  _moveHand(poses) {
    this._moveTween = handMoveTw(this, poses);
    this._moveTween.onComplete.addOnce(() => {
      this.game.add
        .tween(this)
        .to({ alpha: 0 }, 300, Phaser.Easing.Cubic.InOut, true)
        .onComplete.add(() => {
          this._firstTimeShow();
        });
    });
  }

  _hide() {
    this._removeTw();
    this.visible = false;
  }

  _getHintPositions() {
    const boardView = getViewByProperty('name', 'BoardView');
    const rawPoints = getHintPoints().reverse();

    const localPoses = rawPoints.map(rawPoint =>
      this.parent.toLocal(boardView.toGlobal(new Phaser.Point(rawPoint.x, rawPoint.y)), undefined)
    );

    return localPoses;
  }

  _removeTw() {
    this.game.tweens.removeFrom(this.scale);
    this.game.tweens.removeFrom(this);
  }
}
