import { GameOverReasons } from '../../constants';
import { delayRunnable, removeRunnable } from '../../utils';
import { ObservableModel } from '../observable-model';

const ctaTypesDependsOnReason = {
  [GameOverReasons.Idled]: 'standard',
  [GameOverReasons.ItemsToCtaReached]: 'standard'
};

export class CtaModel extends ObservableModel {
  constructor() {
    super('CtaModel');

    this._type = null;
    this._visible = null;
    this._preVisible = null;
    this._reason = GameOverReasons.Unknown;

    this.makeObservable();
  }

  get type() {
    return this._type;
  }

  get reason() {
    return this._reason;
  }

  get preVisible() {
    return this._preVisible;
  }

  get visible() {
    return this._visible;
  }

  destroy() {
    removeRunnable(this._visibilityDelayTimer);
  }

  show(reason, delay = 0) {
    this._reason = reason;
    this._preVisible = true;
    this._type = ctaTypesDependsOnReason[reason]; // || CI_API.Globals.PARAMS.cta_type;

    removeRunnable(this._visibilityDelayTimer);
    this._visibilityDelayTimer = delayRunnable(delay, () => {
      this._visible = true;
    });
  }
}
