import { lego } from '@armathai/lego';
import { hintParamGuard } from '../guards/ad/hint-param-guard';
import { introParamGuard } from '../guards/ad/intro-param-guard';
import { persistentCtaParamGuard } from '../guards/ad/persistent-cta-param-guard';
import { soundParamGuard } from '../guards/ad/sound-param-guard';
import { tutorialParamGuard } from '../guards/ad/tutorial-param-guard';
import { initializeCtaModelCommand } from './ad/cta/initialize-cta-model-command';
import { initializeHintModelCommand } from './ad/hint/initialize-hint-model-command';
import { startHintVisibilityTimerCommand } from './ad/hint/start-hint-visibility-timer-command';
import { initializePersistentCtaModelCommand } from './ad/pcta/initialize-persistent-cta-model-command';
import { initializeSoundModelCommand } from './ad/sound/initialize-sound-model-command';
import { initializeIntroModelCommand } from './ad/tutorial/initialize-intro-model-command';
import { initializeTutorialModelCommand } from './ad/tutorial/initialize-tutorial-model-command';
import { initializeGameModelCommand } from './game/initialize-game-model-command';
import { initializePlayerModelCommand } from './player/initialize-player-model-command';

export function initializeModelsCommand() {
  lego.command
    .execute(initializePlayerModelCommand)

    .execute(initializeGameModelCommand)

    .execute(initializeCtaModelCommand)

    .guard(soundParamGuard)
    .execute(initializeSoundModelCommand)

    .guard(hintParamGuard)
    .execute(initializeHintModelCommand, startHintVisibilityTimerCommand)

    .guard(introParamGuard)
    .execute(initializeIntroModelCommand)

    .guard(tutorialParamGuard)
    .execute(initializeTutorialModelCommand)

    .guard(persistentCtaParamGuard)
    .execute(initializePersistentCtaModelCommand);
}
