/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCtaPlayButtonConfig } from '../../configs/button-configs';
import { getCTAGridConfig } from '../../configs/grid-configs';
import { getCtaShapeImageConfig } from '../../configs/image-configs';
import { getCTABoxTextConfig, getIntroTextConfig, getTutorialTextConfig } from '../../configs/text-configs';
import { AdStatus, GameOverReasons, InputPriority } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import moduleBindings from '../../module-bindings';
import {
  fitText,
  isSquareLikeScreen,
  makeImage,
  makePixel,
  makeText,
  postRunnable,
  pulse,
  showFromBottom,
  showFromRight
} from '../../utils';
import { Button } from '../../utils/button/button';

export class CTAView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._blocker = null;
    this.bindings = moduleBindings;

    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
    lego.event.on(ViewEvents.GameView.ResultHideComplete, this._onResultHideComplete, this);
  }

  getGridConfig() {
    return getCTAGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);

    this._pulsePlayButton();
  }

  _build(blockerAlpha = 0) {
    super.build(this.getGridConfig());
    this._buildBlocker(blockerAlpha);
    this._buildBox();
    this._buildPlayButton(getCtaPlayButtonConfig(CI_API.Globals.PARAMS.cta_btn_text));
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this._build();
        this._showPlayButton();
        this._pulsePlayButton();
        break;
      case AdStatus.Game:
        this.removeChildren();
        break;
      default:
    }
  }

  _onResultHideComplete() {
    this._buildShape();
  }

  _buildShape() {
    if (CI_API.Globals.PARAMS.cta_type === 'embedded') {
      this._shape = makeImage(getCtaShapeImageConfig());
      this._shape.alpha = 0;

      this.setChild('shape', this._shape);
      this.bringToTop(this._playButton);

      postRunnable(() => {
        showFromRight(this._shape, 500).onStart.add(() => {
          this._shape.alpha = 1;
          this._buildLabels();
        });
      });
    }
  }

  _buildLabels() {
    this._buildLabel0();
    this._buildLabel1();
  }

  _buildLabel0() {
    const label0 = makeText(getIntroTextConfig('Embedded CTA label0'));
    label0.alpha = 0;
    this.setChild(`label_0`, (this._label0 = label0));
    this.game.add.tween(this._label0).to({ alpha: 1 }, 300, Phaser.Easing.Sinusoidal.InOut, true, 500, 0, false);
  }

  _buildLabel1() {
    const label1 = makeText(getTutorialTextConfig('Embedded CTA label1'));

    label1.anchor.set(0.5);
    fitText(label1, 300 * 0.8, 300 * 0.8);
    label1.alpha = 0;
    this.setChild(`label_1`, (this._label1 = label1));

    this.game.add.tween(this._label1).to({ alpha: 1 }, 300, Phaser.Easing.Sinusoidal.InOut, true, 500, 0, false);
  }

  _buildBlocker(alpha) {
    this._blocker = makePixel({ alpha });
    this._blocker.inputEnabled = true;
    this._blocker.input.priorityID = InputPriority.Cta;
    this._blocker.name = 'cta background';
    this.setChild('blocker', this._blocker);

    if (CI_API.Globals.PARAMS.cta_scrn_clickable) {
      this._blocker.events.onInputDown.add(this._onScreenClick, this);
    }
  }

  _buildBox() {
    if (CI_API.Globals.PARAMS.cta_type !== 'embedded') {
      this._buildBoxBlocker();
      this._buildBoxLabel();
    }
  }

  _buildBoxBlocker() {
    this._boxBg = makePixel({ alpha: 0 });
    this.setChild('box_bg', this._boxBg);
    this.game.add.tween(this._boxBg).to({ alpha: 0.5 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 100, 0, false);
  }

  _buildBoxLabel() {
    const text = store.ad.cta.reason === GameOverReasons.ItemsToCtaReached ? 'Great Job!' : 'Keep Trying';
    const label = makeText(getCTABoxTextConfig(text));
    label.alpha = 0;
    fitText(label, 450, 300);
    this.setChild('box_label', (this._boxLabel = label));
    this.game.add.tween(this._boxLabel).to({ alpha: 1 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 100, 0, false);
  }

  _buildPlayButton(config) {
    this._playButton = this._buildButton(config, this._onPlayClick);
    this._playButton.alpha = 0;

    this.setChild('play_button', this._playButton);
  }

  _showPlayButton() {
    postRunnable(() => {
      this._playButton.alpha = 1;
      LP(isSquareLikeScreen() ? showFromBottom : showFromRight, showFromBottom).call(this, this._playButton, 500, 800);
    });
  }

  _pulsePlayButton() {
    postRunnable(() => {
      this._pulseTw = pulse(this._playButton, 0.05);
      this._pulseTw.universal = false;
    });
  }

  _buildButton(config, callback) {
    const btn = new Button(config);
    btn.onClick.add(callback, this);
    return btn;
  }

  _onScreenClick() {
    lego.event.emit(ViewEvents.CtaView.ScreenClick);
  }

  _onPlayClick() {
    lego.event.emit(ViewEvents.CtaView.PlayClick);
  }
}
